* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#root {
  width: 100vw;
  height: 100vh;
}

main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

header {
  width: 100%;
  height: 10%;
  background-color: #2e2d2d;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

header form {
  width: 300px;
  height: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

header form input {
  margin-left: 10px;
  width: 200px;
  height: 40px;
  border: none;
  outline: none;
  color: #2e2d2d;
  padding: 5px;
  font-size: 1.2rem;
}

header form button {
  width: 60px;
  height: 40px;
  border: none;
  outline: none;
  background-color: #440202;
  color: whitesmoke;
  padding: 5px;
  margin-left: 10px;
  cursor: pointer;
}

header h2 {
  text-transform: capitalize;
  margin-left: 150px;
  color: whitesmoke;
}

.pages {
  width: 300px;
  height: 40px;
  margin-left: 100px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.pages p {
  font-size: 1.1rem;
  color: whitesmoke;
}

aside {
  width: 80%;
  height: 40px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

aside button {
  width: 100px;
  height: 40px;
  margin-right: 30px;
  border: none;
  outline: none;
  padding: 5px;
  margin-left: 5px;
  background-color: #440202;
  color: whitesmoke;
  cursor: pointer;
}

.content {
  width: 100%;
  height: 80%;
  background-color: #1f0101;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.messages {
  width: 100%;
  height: 80%;
  background-color: #1f0101;
  display: flex;
  justify-content: center;
  align-items: center;
}

.message-text {
  font-size: 2rem;
  color: whitesmoke;
}

img {
  object-fit: contain;
  margin: 5px;
  width: 200px;
  height: 200px;
}

footer {
  width: 100%;
  height: 10%;
  background-color: #2e2d2d;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

footer p {
  margin-left: 10px;
  color: whitesmoke;
}
